@import "shared/styles/variables";

.root {
  padding: 60px 0;
  min-height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  width: 75px;
  height: 29px;
  path {
    fill: var(--main-color);
  }
  line {
    stroke: var(--main-color);
  }
}